import {
    LOGIN_IN,
    LOGIN_OUT,
    SELECT_FARM
} from './actionTypes';

interface LoginIn {
    token: string;
    loginStatus: boolean;
}

interface SelectFarm {
    farmCode: string;
}
interface Action {
    type: string;
    payload?: object;
}

export const loginIn = (data: LoginIn): Action => ({
    type: LOGIN_IN,
    payload: {
        ...data
    }
});
export const loginOut = (): Action => ({
    type: LOGIN_OUT
});
export const selectFarm = (data: SelectFarm): Action => ({
    type: SELECT_FARM,
    payload: {
        ...data
    }
});
