import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Link as RouterLink, Redirect } from 'react-router-dom';
import {
    Container,
    InputAdornment,
    Grid,
    Link,
    IconButton,
    FormControl,
    InputLabel,
    Input
} from '@material-ui/core';
import { Visibility, VisibilityOff } from '@material-ui/icons';
import { from, throwError } from 'rxjs';
import { ajax } from 'rxjs/ajax';
import { map, concatMap, catchError } from 'rxjs/operators';
import { loginIn as LoginIn } from '@/redux/actions';
import { loginIn$ } from '@/api';
import useStyles from './styles';
import MButton from '@/components/button';
import qs from 'qs';
import { baseHost2 } from '@/config';
import schema from 'async-validator';
import useMessage from '@/hooks/useMessage';

interface State {
    password: string;
    username: string;
}
interface LoginIn {
    token: string;
    loginStatus: boolean;
}

const descriptor = {
    username: {
        validator: (rule: any, value: string) => {
            if (!value) {
                return new Error('请输入手机号');
            }
            if (!/^1[34578]\d{9}$/.test(value)) {
                return new Error('手机号格式有误');
            }
            return true;
        }
    },
    password: {
        required: true,
        type: 'string',
        message: '请输入密码'
    }
};
const validator = new schema(descriptor);

const Login: React.FC = (props: any) => {
    const loginStatus = useSelector((state: any) => state.User.loginStatus);
    const dispatch = useDispatch();
    const showMessage = useMessage();
    const [loginInfo, setLoginInfo] = useState<State>({
        username: '13482545420',
        password: '123456'
    });
    const classes = useStyles();
    const [loading, setLoading] = useState(false);
    const [showPassword, setShowPassword] = useState(false);
   
    const loginIn = (data: LoginIn) => dispatch(LoginIn(data));
    const handleUserInfoChange = (name: keyof State) => (e: React.ChangeEvent<HTMLInputElement>) => {
        setLoginInfo({...loginInfo, [name]: e.target.value})
    };
    const getCode = () => {
        return ajax({
            url: `${baseHost2}/mobile/login/`,
            method: 'post',
            body: qs.stringify({
                username: loginInfo.username,
                password: loginInfo.password,
                client_id: '611753',
                redirect_uri: 'http://aladdin.cas.cx',
                response_type: 'code',
                scope: 'openid profile email phone address ',
                state: ''
            })
        })
    }
    const getToken = (code: string | number) => {
        return loginIn$({ code })
    };
    const loginAuth = () => {
        setLoading(true);
        const validatorObser = from(validator.validate(loginInfo, () => {}));
        const apiInst = validatorObser.pipe(
            catchError((error: any) => {
                const message = error.errors.map((item: any) => item.message)[0];
                showMessage({ variant: 'warning', message });
                return throwError(error);
            }),
            concatMap(() => {
                return getCode().pipe(
                    map(({ response }) => response)
                );
            }),
            concatMap(response => {
                return getToken(response.code).pipe(
                    map(({ response }) => response)
                )
            }),
            catchError((error: any) => {
                setLoading(false);
                return throwError(error);
            })
        ).subscribe(
            data => {
                loginIn({
                    token: data.token,
                    loginStatus: true
                });
                apiInst.unsubscribe();
                dispatch({ type: 'CLOSE_MESSAGE' });
                props.history.push('/selectRanch');
            },
            error => {
                console.log('error', error);
                apiInst.unsubscribe();
            }
        );
    };

    return (
        <Container >
            {/* <If condition={loginStatus}>
                <Redirect to="/home" />
            </If> */}
            <h1 className={`${classes.loginTitle} text-center`}>
                登录阿拉钉牧场
            </h1>
            <h4 className={`${classes.loginSubTitle}  text-center`}>
                {/* 请用您的手机号登陆 */}
                欢迎您使用阿拉钉牧场平台
            </h4>
            <div className={classes.formContainer}>
                <FormControl
                    fullWidth
                    className={classes.textField}
                >
                    <InputLabel htmlFor="adornment-mobile">手机号</InputLabel>
                    <Input
                        id="adornment-mobile"
                        value={loginInfo.username}
                        onChange={handleUserInfoChange('username')}
                    />
                </FormControl>
                <FormControl
                    fullWidth
                    className={classes.textField}
                >
                    <InputLabel htmlFor="adornment-password">密码</InputLabel>
                    <Input
                        id="adornment-password"
                        type={showPassword ? 'text' : 'password'}
                        value={loginInfo.password}
                        onChange={handleUserInfoChange('password')}
                        endAdornment={
                            <InputAdornment position="end">
                                <IconButton
                                    aria-label="toggle password visibility"
                                    onClick={() => setShowPassword(!showPassword)}
                                    onMouseDown={e => e.preventDefault()}
                                >
                                    <If condition={showPassword}>
                                        <Visibility color="primary" />
                                    </If>
                                    <If condition={!showPassword}>
                                        <VisibilityOff  />
                                    </If>
                                </IconButton>
                            </InputAdornment>
                        }
                    />
                </FormControl>
 
                <MButton
                    className={classes.textField}
                    loading={loading}
                    loadingTxt="登录中"
                    onClick={loginAuth}
                    btnText="登录"
                />
            </div>
            <Grid container className={classes.register}>
                <Grid item xs={2} >
                    
                </Grid>
                <Grid item xs={6} >
                    <span>我还没有账号</span>
                </Grid>
                <Grid item xs={4}>
                    <Link component={RouterLink} to="/register"> 我要注册 </Link>
                </Grid>
            </Grid>
        </Container>
    );
}
export default Login;