/**
 * Home Page other page entrance area
 */
import React, { useState } from "react";
import { useDispatch } from 'react-redux';
import { Link as RouterLink } from 'react-router-dom';
import { Container, Grid, Link } from '@material-ui/core';
import { Description } from '@material-ui/icons';
import Search from '@/components/search/index';
import useStyles from "./_styles";
import { fetchCalfsList$ } from '@/api';
import { map } from 'rxjs/operators';

interface IProps {
    isALD: string;
}

const Entrance: React.FC<IProps> = (props: any) => {
    const classes = useStyles();
    const dispatch = useDispatch();
    const [searchLoading, setSearchLoading] = useState(false);

    const searchMethod = (eartag: string) => {
        console.log('eartag', eartag)
        if (!eartag) {
            dispatch({
                type: 'SHOW_MESSAGE',
                payload: {
                    open: true,
                    message: '请输入牛号',
                    variant: 'warning'
                }
            });
            return;
        }
        const params = {
            page: 1,
            page_size: 10,
            eartag
        };
        setSearchLoading(true);
        const apiInst = fetchCalfsList$(params).pipe(
            map(({ response }) => response)
        ).subscribe(
            (data: any) => {
                console.log('data', data);
                apiInst.unsubscribe();
                setSearchLoading(false);
                if (data.count === 1) {
                    const { id, eartag } = data.results[0];
                    props.history.push(`/cowDetail/${id}?eartag=${eartag}`)
                } else {
                    dispatch({
                        type: 'SHOW_MESSAGE',
                        payload: {
                            open: true,
                            message: '未搜到该牛号的牛只!',
                            variant: 'warning'
                        }
                    });
                }
            },
            error => {
                setSearchLoading(true);
            }
        );
    };

    return (
        <Container className={classes.homeContent}>
            <div
                className={classes.searchContainer}
            >
                <Search
                    placeholder="搜索牛只"
                    searchMethod={searchMethod}
                    loading={searchLoading}
                    searchOnValueIsEmpty={false}
                />
            </div>
            <Grid justify="center" container >
                <Choose>
                    <When condition={props.isALD === "1"}>
                        <Grid item xs={6} >
                                <Link component={RouterLink} to="/manageList/ald">
                                    <div className="home-entrance-wrapper">
                                        <Description className="fs-36-it" />
                                        <div >
                                            阿拉钉评估
                                        </div>
                                    </div>
                                </Link>
                        </Grid>
                    </When>
                    <Otherwise>
                        <Grid item xs={6}>
                            <Link component={RouterLink} to="/manageList/society">
                                <div className="home-entrance-wrapper">
                                    <Description className="fs-36-it" />
                                    <div>
                                        社会牧场评估
                                    </div>
                                </div>
                            </Link>
                        </Grid>
                    </Otherwise>
                </Choose>
                <Grid item xs={6}>
                    <Link component={RouterLink} to="/tasks">
                        <div className="home-entrance-wrapper">
                            <Description className="fs-36-it" />
                            <div>
                                任务清单
                            </div>
                        </div>
                    </Link>
                </Grid>
                <Grid item xs={6}>
                    <Link component={RouterLink} to="/sceneReport">
                        <div className="home-entrance-wrapper">
                            <Description className="fs-36-it" />
                            <div>
                                现场报告
                            </div>
                        </div>
                    </Link>
                </Grid>
                <Grid item xs={6}>
                    <Link component={RouterLink} to="/sceneReport">
                        <div className="home-entrance-wrapper">
                            <Description className="fs-36-it" />
                            <div>
                                分析报告
                            </div>
                        </div>
                    </Link>
                </Grid>
            </Grid>
        </Container>
    );
};
export default Entrance;