import React, { useEffect, useRef, useState, useCallback } from 'react';
import {
    Grid,
    CircularProgress,
    IconButton,
    Paper,
    InputBase
} from '@material-ui/core';
import {
    ArrowBackIos,
    Search as SearchIcon
} from '@material-ui/icons';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import useStyles from './styles';
import { fromEvent } from 'rxjs';
import { map } from 'rxjs/operators';

interface IProps extends RouteComponentProps {
    title: string;
    endAdorment?: JSX.Element;
    useSearch?: boolean;
    autofocus?: boolean;
    searchPlaceholder?: string;
    searchLoading?: boolean;
    getSearchValue?: (val: string) => void;
    beforeRouteLeave?: () => void;
}

const  AppBar: React.FC<IProps> = props => {
    const classes = useStyles();
    const [value, setValue] = useState('');
    const inputRef = useRef(null);

    const handleClickBack = () => {
        props.beforeRouteLeave && props.beforeRouteLeave();
        props.history.goBack();
    };
    const handleClickSearch = useCallback(() => {
        return props.getSearchValue && props.getSearchValue(value);
    }, [props, value]);

    useEffect(() => {
        const current: any = inputRef.current;
        let inputSub: any = null;
        inputSub = current && fromEvent(current, 'search').pipe(
            map((e: any) => e.target.value)
        ).subscribe(
            value => {
                console.log('value', value)
                props.getSearchValue && props.getSearchValue(value);
            }
        );
        return () => inputSub && inputSub.unsubscribe();
    },[inputRef, handleClickSearch, props]);

    return (
        <Paper className={classes.root} square={true}>
            <Grid container alignItems="center">
                <Grid item xs={2}>
                    <IconButton onClick={() => handleClickBack()}>
                        <ArrowBackIos className={classes.icon} />
                    </IconButton>
                </Grid>
                <Choose>
                    <When condition={!props.useSearch}>
                        <Grid item xs>
                            {props.title}
                        </Grid>
                        <Grid item xs={2}>
                            {props.endAdorment}
                        </Grid>
                    </When>
                    <Otherwise>
                        <Grid item xs="auto" className={classes.inputBox}>
                            <InputBase
                                autoFocus={props.autofocus}
                                type="search"
                                inputRef={inputRef}
                                value={value}
                                onChange={e => setValue(e.target.value)}
                                className={classes.input}
                                placeholder={props.searchPlaceholder}
                                inputProps={{ 'aria-label': props.searchPlaceholder }}
                            />
                            <Choose>
                                <When condition={props.searchLoading}>
                                    <IconButton
                                        className={classes.iconButton}
                                        aria-label="Search"
                                    >
                                        <CircularProgress size={20} color="secondary" />
                                    </IconButton>
                                </When>
                                <Otherwise>
                                <IconButton
                                        className={classes.iconButton}
                                        aria-label="Search"
                                        onClick={handleClickSearch}
                                    >
                                        <SearchIcon
                                             color="secondary"
                                        />
                                    </IconButton>
                                </Otherwise>
                            </Choose>
                        </Grid>
                    </Otherwise>
                </Choose>
            </Grid>
        </Paper>
    )
}

export default withRouter(AppBar);