import React from 'react';
// import qs from "querystring";
import { useSelector } from 'react-redux';
import { Redirect } from 'react-router-dom';
import url from "url";
import { makeStyles } from '@material-ui/styles';
import HomeHeader from "./header";
import Entrance from "./entrance";
import Reports from "./report";
import "./index.scss";

const useStyles = makeStyles({
    homeContainer: {
        background: '#fff'
    }
});

interface IProps{ 
    isLogin: boolean;
}

const Home: React.FC<IProps> = (props: any) => {
    const classes = useStyles();
    const loginStatus = useSelector((state: any) => state.User.loginStatus);
    const { query } = url.parse(props.location.search, true);
    const isALD= String(query.isALD || sessionStorage.getItem("isALD") || "0");

    return (
        <div className={classes.homeContainer}>
            <If condition={!loginStatus}>
                <Redirect to="/login" />
            </If>
            <HomeHeader {...props} />
            <Entrance isALD={isALD} {...props} />
            <Reports />
        </div>
    )
};
export default Home;