import React, { useState, useEffect } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import {
    Container,
    ButtonGroup,
    Grid,
    Link,
    InputAdornment,
    Button,
    InputLabel,
    Input,
    FormControl,
    IconButton
} from '@material-ui/core';
import { Visibility, VisibilityOff } from '@material-ui/icons';
import qs from 'qs';
import useStyles from './styles';
import MButton from '@/components/button';
import { from, EMPTY, Subscription } from 'rxjs';
import { concatMap, catchError, pluck, tap } from 'rxjs/operators';
import { ajax } from 'rxjs/ajax';
import { baseHost2 } from '@/config';
import useMessage from '@/hooks/useMessage';
import schema from 'async-validator';

interface State {
    phone_number: string;
    password: string;
    confirm_password: string;
    company_name: string;
    verify_code: string;
}
const descriptorPhoneNumber = {
    phone_number: {
        validator: (rule: any, value: string) => {
            if (!value) {
                return new Error('请输入手机号');
            }
            if (!/^1[34578]\d{9}$/.test(value)) {
                return new Error('手机号格式有误');
            }
            return true;
        }
    }
};
const descriptor = {
    ...descriptorPhoneNumber,
    verify_code: {
        required: true,
        type: 'string',
        message: '请输入验证码'
    },
    password: {
        required: true,
        type: 'string',
        message: '请输入密码'
    },
    confirm_password(rule: any, value: string, callback: any, source: State) {
        if (!value) {
            return new Error('请输入确认密码');
        }
        if (value !== source.password) {
            return new Error('两次密码不一致')
        }
        return true;
    },
    company_name: {
        required: true,
        type: 'string',
        message: '请输入公司名称'
    }
};
const validator = new schema(descriptor);
const validatorPhoneNumber = new schema(descriptorPhoneNumber);

const Register: React.FC = (props: any) => {
    const classes = useStyles();
    const [RegisterForm, setRegisterForm] = useState<State>({
        phone_number: '',
        password: '',
        confirm_password: '',
        company_name: '',
        verify_code: ''
    });
    const [hasRegister, setHasRegister] = useState(false);
    const [btnDisabledState, setBtnDisabledState] = useState(false);
    const [codeTime, setCodeTime] = useState(30);
    const [btnTxt, setBtnTxt] = useState('获取验证码');
    const [loading, setLoading] = useState(false);
    const [showPassword, setShowPassword] = useState(false);
    const [showConfirmPassword, setConfirmShowPassword] = useState(false);
    const showMessage = useMessage();

    const handleGetMobileCode = () => {
        setBtnDisabledState(true);
        let apiInst: Subscription;
        const Params = {
            method: 'get',
            url: `${baseHost2}/accounts/check_phone_code/${RegisterForm.phone_number}/`,
            crossDomain: true,
            withCredentials: true
        };
        apiInst = from(
            validatorPhoneNumber.validate(
                { phone_number: RegisterForm.phone_number },
                () => {}
            )
        ).pipe(
            catchError(error => {
                const message = error.errors[0].message;
                showMessage({ variant: 'warning', message });
                setBtnDisabledState(false);
                return EMPTY;
            }),
            concatMap(() => ajax(Params).pipe(tap(data => console.log('data', data))))
        ).subscribe(
            () => {
                apiInst.unsubscribe();
                showMessage({ variant: 'success', message: '发送成功，请注意查收！'});
            },
            error => {
                console.log('error', error);
                apiInst.unsubscribe();
                setBtnDisabledState(false);
                showMessage({ variant: 'error', message: error});
            }
        )
    };
    const handleRegisterFormChange = (name: keyof State) => (e: React.ChangeEvent<HTMLInputElement>) => {
        setRegisterForm({ ...RegisterForm, [name]: e.target.value });
    }
    const handleRegister = () => {
        setLoading(true);
        const Params = {
            method: 'post', 
            url: `${baseHost2}/mobile/reg/`,
            crossDomain: true,
            withCredentials: false,
            body: qs.stringify(RegisterForm)
        };
        const apiInst = from(validator.validate(RegisterForm, () => {})).pipe(
            catchError(error => {
                const message = error.errors[0].message;
                showMessage({ variant: 'warning', message });
                setLoading(false);
                return EMPTY;
            }),
            concatMap(() => ajax(Params).pipe(pluck('response')))
        ).subscribe(
            data => {
                setLoading(false);
                apiInst.unsubscribe();
                if (data.success) {
                    showMessage({ variant: 'success', message: '注册成功!' });
                    setHasRegister(true)
                } else {
                    showMessage({ variant: 'error', message: data.error_msg });
                }
            },
            error => {
                apiInst.unsubscribe();
                setLoading(false);
                showMessage({ variant: 'error', message: error});
            }
        );
    }

    useEffect(() => {
        let timerId: any;
        if (btnDisabledState) {
            setBtnTxt(`${codeTime}s`);
            timerId = setInterval(() => {
                clearInterval(timerId);
                if (codeTime !== 0) {
                    setCodeTime(codeTime => codeTime - 1);
                } else {
                    setBtnDisabledState(false);
                    setCodeTime(30);
                }
            }, 1000);
        } else {
            setBtnTxt('获取验证码');
        }
        return () => clearInterval(timerId);
    }, [btnDisabledState, codeTime]);

    return (
        <Container >
            <h1 className={`${classes.loginTitle} text-center`}>
                阿拉钉牧业通用平台
            </h1>
            <Choose>
                <When condition={!hasRegister}>
                    <div className={classes.formContainer}>
                        <FormControl
                            fullWidth
                            className={classes.textField}
                        >
                            <InputLabel htmlFor="adornment-phone_number">手机号</InputLabel>
                            <Input
                                id="adornment-phone_number"
                                value={RegisterForm.phone_number}
                                onChange={handleRegisterFormChange('phone_number')}
                            />
                        </FormControl>
                        <FormControl
                            fullWidth
                            className={classes.textField}
                        >
                            <InputLabel htmlFor="adornment-verify_code">验证码</InputLabel>
                            <Input
                                id="adornment-verify_code"
                                value={RegisterForm.verify_code}
                                onChange={handleRegisterFormChange('verify_code')}
                                endAdornment={
                                    <Button
                                        variant="text"
                                        className={classes.codeBtn}
                                        size="small"
                                        color="primary"
                                        disabled={btnDisabledState}
                                        onClick={handleGetMobileCode}
                                    >
                                        {btnTxt}
                                    </Button>
                                }
                            />
                        </FormControl>
                        <FormControl
                            fullWidth
                            className={classes.textField}
                        >
                            <InputLabel htmlFor="adornment-password">密码</InputLabel>
                            <Input
                                id="adornment-password"
                                type={showPassword ? 'text' : 'password'}
                                value={RegisterForm.password}
                                onChange={handleRegisterFormChange('password')}
                                endAdornment={
                                    <InputAdornment position="end">
                                        <IconButton
                                            aria-label="toggle password visibility"
                                            onClick={() => setShowPassword(!showPassword)}
                                            onMouseDown={e => e.preventDefault()}
                                        >
                                            <If condition={showPassword}>
                                                <Visibility color="primary" />
                                            </If>
                                            <If condition={!showPassword}>
                                                <VisibilityOff  />
                                            </If>
                                        </IconButton>
                                    </InputAdornment>
                                }
                            />
                        </FormControl>
                        <FormControl
                            fullWidth
                            className={classes.textField}
                        >
                            <InputLabel htmlFor="adornment-confirm_password">确认密码</InputLabel>
                            <Input
                                id="adornment-confirm_password"
                                type={showConfirmPassword ? 'text' : 'password'}
                                value={RegisterForm.confirm_password}
                                onChange={handleRegisterFormChange('confirm_password')}
                                endAdornment={
                                    <InputAdornment position="end">
                                        <IconButton
                                            aria-label="toggle confirm_password visibility"
                                            onClick={() => setConfirmShowPassword(!showConfirmPassword)}
                                            onMouseDown={e => e.preventDefault()}
                                        >
                                            <If condition={showConfirmPassword}>
                                                <Visibility color="primary" />
                                            </If>
                                            <If condition={!showConfirmPassword}>
                                                <VisibilityOff  />
                                            </If>
                                        </IconButton>
                                    </InputAdornment>
                                }
                            />
                        </FormControl>
                        <FormControl
                            fullWidth
                            className={classes.textField}
                        >
                            <InputLabel htmlFor="adornment-company_name">请输入企业名称</InputLabel>
                            <Input
                                id="adornment-company_name"
                                value={RegisterForm.company_name}
                                onChange={handleRegisterFormChange('company_name')}
                            />
                        </FormControl>
                        <MButton
                            className={classes.registerbtn}
                            loading={loading}
                            loadingTxt="注册中"
                            onClick={handleRegister}
                            btnText="注册"
                        />
                        <Grid container className={classes.register}>
                            <Grid item xs={1} >
                                
                            </Grid>
                            <Grid item xs={7} >
                                <span>已经有账户</span>
                            </Grid>
                            <Grid item xs={4} className="text-center">
                                <Link component={RouterLink} to="/login">登录</Link>
                            </Grid>
                        </Grid>
                    </div>
                </When>
                <Otherwise>
                    <div className={classes.registerSuccess}>
                        <h3>欢迎使用，有任何问题请致电：</h3>
                        <h3>18621812625</h3>
                        <ButtonGroup
                            className={classes.textField}
                            fullWidth
                            size="medium"
                            variant="contained"
                            color="primary"
                            aria-label="Full width outlined button group"
                            onClick={() => props.history.push('/login')}
                        >
                            <Button>
                                继续
                            </Button>
                        </ButtonGroup>
                    </div>
                </Otherwise>
            </Choose>
        </Container>
    );
}
export default Register;