import {
    LOGIN_IN,
    LOGIN_OUT,
    SELECT_FARM
} from './actionTypes';
import { getUserLocal, setUserLocal } from '@/config';

interface Action {
    type: string;
    payload?: object;
}
interface User {
    token: string,
    loginStatus: boolean,
    farmCode: string;
}

const { token = '', loginStatus = false, farmCode = '' }: User = getUserLocal();
const defaultState = () => ({ token,loginStatus,farmCode });

export default function(state: User = defaultState(), action: Action) {
    const emptyState = {
        token: '',
        loginStatus: false,
        farmCode: ''
    };
    const { payload } = action;

    switch(action.type) {
        case LOGIN_IN: {
            setUserLocal(payload);
            return {
                ...state,
                ...payload
            }
        }
        case LOGIN_OUT: {
            setUserLocal(emptyState);
            return {
                token: '',
                loginStatus: false,
                farmCode: ''
            }
        }
        case SELECT_FARM: {
            setUserLocal(payload);
            return {
                ...state,
                ...payload
            }
        }
        default:
            return state
    }
}
