import React, { useState, useRef, useEffect } from 'react';
import { makeStyles } from '@material-ui/styles';
import { CircularProgress } from '@material-ui/core';
import Paper from '@material-ui/core/Paper';
import InputBase from '@material-ui/core/InputBase';
import IconButton from '@material-ui/core/IconButton';
import SearchIcon from '@material-ui/icons/Search';
import { fromEvent } from 'rxjs';
import { map } from 'rxjs/operators';

const useStyles = makeStyles({
    root: {
        padding: '2px 4px',
        display: 'flex',
        alignItems: 'center',
        backgroundColor: '#fff'
        // marginBottom: 20
    },
    input: {
        marginLeft: 8,
        flex: 1,
    },
    iconButton: {
      padding: 5,
    }
});

interface SearchProps {
    loading?: boolean;
    searchMethod?: any;
    placeholder?: string;
    searchOnValueIsEmpty: boolean;
    onSearchChange?:() => void;
}

const Search: React.FC<SearchProps> = props => {
    const classes = useStyles();
    const [value, setValue] = useState('');
    const inputRef = useRef(null);

    const handleClickSearch = () => {
        if (!props.searchOnValueIsEmpty && !value) {
            return;
        }
        props.searchMethod && props.searchMethod(value);
    };

    useEffect(() => {
        const current: any = inputRef.current;
        const inputSub = fromEvent(current, 'search').pipe(
            map((e: any) => e.target.value)
        ).subscribe(
            value => {
                console.log('value', value)
                if(!props.searchOnValueIsEmpty && !value) return;
                props.searchMethod && props.searchMethod(value);
            }
        );
        return () => inputSub.unsubscribe()
    },[inputRef, handleClickSearch]);

    return (
        <Paper className={classes.root}>
            <InputBase
                className={classes.input}
                placeholder={props.placeholder}
                inputProps={{ 'aria-label': '搜索阿拉钉牧场' }}
                inputRef={inputRef}
                value={value}
                onChange={e => setValue(e.target.value)}
                type="search"
            />
            <Choose>
                <When condition={props.loading}>
                    <IconButton
                        className={classes.iconButton}
                        aria-label="Search"
                        style={{
                            height: '34px'
                        }}
                    >
                        <CircularProgress size={21} color="primary" />
                    </IconButton>
                </When>
                <Otherwise>
                <IconButton
                        className={classes.iconButton}
                        aria-label="Search"
                        style={{
                            height: '34px'
                        }}
                        onClick={handleClickSearch}
                    >
                        <SearchIcon
                            color={ value ? 'primary' : 'disabled'}
                        />
                    </IconButton>
                </Otherwise>
            </Choose>
        </Paper>
    )
}
export default Search;