import { ajax, AjaxResponse } from 'rxjs/ajax';
import { catchError } from 'rxjs/operators';
import { EMPTY, throwError } from 'rxjs';
import { baseUrl, getUserLocal } from '@/config';
import { showMessage, loginOut } from '@/redux/actions';
import store from '@/redux/store';

const dispatchMessage = (message: string) => store.dispatch(showMessage({ variant: 'error', message}));
const dispatchToLogin = () => {
    store.dispatch(loginOut());
    window.location.href=`${window.location.origin}/#/login`;
};
const handleError = (error: AjaxResponse) => {
    console.log('handleError', error);
    if (error && error.status) {
        switch (error.status) {
          case 400:
            dispatchMessage('参数错误');
            break
          case 401:
            dispatchMessage('未授权，请登录');
            dispatchToLogin();
            break
          case 402:
            dispatchMessage('登录过期，请重新登录');
            dispatchToLogin();
            break
          case 403: dispatchMessage('拒绝访问'); break
          case 404: dispatchMessage('请求地址出错'); break
          case 408: dispatchMessage('请求超时'); break
          case 500: dispatchMessage('服务器内部错误'); break
          case 501: dispatchMessage('服务未实现'); break
          case 502: dispatchMessage('网关错误'); break
          case 503: dispatchMessage('服务不可用'); break
          case 504: dispatchMessage('网关超时'); break
          case 505: dispatchMessage('HTTP版本不受支持'); break
          default: break
        }
    }
    return throwError(error);
};
export default {
    get(url: string, params: any, options?: ApiOptions) {
        let initUrl = `${baseUrl}${url}/`;
        let headers: ApiHeaders = {};
        let formaturl: string = '';
        let { token, farmCode } = getUserLocal();
        const globalState = store.getState();
        
        if (options && options.useToken) {
            if (token && globalState.User.token) {
                headers.AUTHORIZATION = `JWT ${token}`;
            } else{
                dispatchToLogin();
                return EMPTY;
            }
        }
        if(options && options.useFarmCode) {
            if (!farmCode) {
                dispatchToLogin();
                return EMPTY;
            }
            initUrl = `${baseUrl}${farmCode}/${url}/`;
        }
        if(options && options.useId && options.useFarmCode) {
            const { id } = params;
            initUrl = `${baseUrl}${farmCode}/${url}/${id}/`;
            delete params.id;
        }
        if(options && options.overrideUrl) {
            initUrl = options.overrideUrl(baseUrl, url, { farmCode });
        }
        
        formaturl = `${initUrl}?${Object.entries(params).map(item => item.join('=')).join('&')}`;

        return ajax.get(formaturl, headers).pipe(
            catchError(handleError)
        );
    },
    post(url: string, params: object | string, options?: ApiOptions) {
        let headers: ApiHeaders = {};
        let initUrl = `${baseUrl}${url}/`;
        let { token, farmCode } = getUserLocal();
        const globalState = store.getState();
        
        if (options && options.useToken) {
            if (token && globalState.User.token) {
                headers.AUTHORIZATION = `JWT ${token}`;
            } else{
                dispatchToLogin();
                return EMPTY;
            }
        }
        if(options && options.useFarmCode) {
            if (!farmCode) {
                dispatchToLogin();
                return EMPTY;
            }
            initUrl = `${baseUrl}${farmCode}/${url}/`
        }
        if(options && options.overrideUrl) {
            initUrl = options.overrideUrl(baseUrl, url, { farmCode });
        }
        if(options && options.useJSON) {
            headers['content-type'] = 'application/json';
        }
        if(options && options.overrideHeaders) {
            headers = options.overrideHeaders(headers);
        }
        return ajax.post(initUrl, params, headers).pipe(
            catchError(handleError)
        );
    }
}