import React,{ useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { map } from 'rxjs/operators';
import {
    Grid,
    Switch,
    TextField,
    Button,
    ButtonGroup,
    Radio,
    RadioGroup,
    FormControl,
    FormControlLabel
} from '@material-ui/core';
import { selectFarm as SelectFarm } from '@/redux/actions';
import Search from '@/components/search/index';
import { fetchCanUseFarms$ } from '@/api';
import useStyles from './styles';
import "./index.scss";

interface aldRanchItem {
    farm_code: string;
    farm_name: string;
}
interface SelectFarm {
    farmCode: string;
}

const SelectRanch = (props: any) => {
    const classes = useStyles();
    const dispatch = useDispatch();

    const [params, setParams] = useState({
        page: 1,
        page_size: 1000,
        search: ''
    });
    const [otherRanchState, setOtherRanchState] = React.useState(false);
    const [checkALD, setCheckALD] = useState(true);
    const [aldRanch, setAldRanch] = useState('');
    const [other, setOtherRanch] = useState('');
    const [aldRanchList, setAldRanchList] = useState<aldRanchItem[]>([]);

    const selectFarm = (data: SelectFarm) => dispatch(SelectFarm(data));
    const handleChange = (e: any) => {
        setCheckALD(e.target.checked)
    };
    const handleALDRanchChange = (e: any) => {
        setAldRanch(e.target.value)
    }
    const handleInputOtherRanch = (e: any) => {
        setOtherRanch(e.target.value);
    }
    const jumpToHome = () => {
        if (checkALD && !aldRanch) return;
        if (!checkALD && !other) return;
        let isALD = "0";
        if (checkALD) {
            isALD = "1";
            selectFarm({farmCode: aldRanch});
        } else {
            setOtherRanchState(!other);
            if (!other) return;
        }
        props.history.push(`/home?isALD=${isALD}`);
        sessionStorage.setItem("isALD", isALD);
    };

    useEffect(() =>  {
        const apiInst =  fetchCanUseFarms$(params).pipe(
            map(({ response = {} }) => (response.results || []))
        ).subscribe(
            data => {
                console.log('data', data);
                setAldRanchList(data);
                setAldRanch((data[0] && data[0].farm_code) || '');
            },
            error => {
                console.log('error', error);
            }
        )
        return () => {
            apiInst.unsubscribe();
        }
    }, [params]);

    return (
        <div className="select-ranch-wrapper">
            <div className="select-ranch-container">
                <Grid container alignItems="center" justify="space-between">
                    <Grid item xs={8}>
                        <h1 className={classes.title}>登陆阿拉钉牧场</h1>
                    </Grid>
                    <Grid item xs={4} className="text-right">
                        <Switch
                            checked={checkALD}
                            color="primary"
                            onChange={handleChange}
                        />
                    </Grid>
                </Grid>
                <Choose>
                    <When condition={checkALD}>
                        <Search
                            searchMethod={(search: string) => setParams({
                                ...params,
                                search
                            })}
                            searchOnValueIsEmpty={true}
                        />
                        <FormControl component="fieldset" className={classes.ranchList}>
                            <RadioGroup aria-label="position" name="position" value={aldRanch} onChange={handleALDRanchChange}>
                                {
                                    aldRanchList.map(item => (
                                        <FormControlLabel
                                            key={item.farm_code}
                                            value={item.farm_code}
                                            control={<Radio color="primary" />}
                                            label={item.farm_name}
                                            labelPlacement="end"
                                        />
                                    ))
                                }
                            </RadioGroup>
                            <If condition={!aldRanchList.length}>
                                <h6 className="text-center">无可用牧场</h6>
                            </If>
                        </FormControl>
                    </When>
                    <Otherwise>
                        <TextField
                            error={otherRanchState}
                            className={classes.textField}
                            placeholder="录入您的牧场名称,如XXX牧场"
                            fullWidth
                            value={other}
                            onChange={handleInputOtherRanch}
                            helperText={otherRanchState ? "请录入您的牧场名称" : ""}
                        />
                    </Otherwise>
                </Choose>
                <ButtonGroup
                    className={classes.textField}
                    fullWidth
                    size="medium"
                    onClick={jumpToHome}
                    variant="contained"
                    color="primary"
                    aria-label="Full width outlined button group"
                    disabled={!aldRanchList.length}
                >
                    <Button>
                        确定
                    </Button>
                </ButtonGroup>
            </div>
        </div>
    )
}

export default SelectRanch;