import React from 'react';
import { Box, CircularProgress } from '@material-ui/core';

interface IProps {
    loading: boolean;
    hasMore: boolean;
    loadMore: () => void;
}
export default function ClickLoadMore(props: IProps) {
    const { loading, loadMore, hasMore } = props;
    return (
        <Choose>
            <When condition={loading}>
                <Box display="flex" justifyContent="center" alignItems="center" className="text-center" marginTop="20px" fontSize="12px">
                    <CircularProgress size={15} style={{ marginRight: 10 }} />正在加载中...
                </Box>
            </When>
            <Otherwise>
                <Box className="text-center" marginTop="20px" onClick={() => loadMore()} fontSize="12px">
                    {hasMore ? '点击加载更多' : '没有更多了'}
                </Box>
            </Otherwise>
        </Choose>
    )
}