import {
    SHOW_MESSAGE,
    CLOSE_MESSAGE
} from './actionTypes';

interface Action {
    type: string;
    payload?: object;
}
interface State {
    variant: string;
    open: boolean;
    message: string;
}

const defaultState = () => ({
    variant: 'error',
    open: false,
    message: ''
});

export default function(state: State = defaultState(), action: Action) {
    const { payload } = action;
    switch (action.type) {
        case SHOW_MESSAGE: {
            return {
                ...state,
                open: true,
                ...payload
            }
        }
        case CLOSE_MESSAGE: {
            return {
                ...state,
                open: false
            }
        }
        default:
            return state
    }
}
