import React, { useState, useEffect, useCallback } from 'react';
import { RouteComponentProps } from 'react-router-dom';
import { useDispatch } from 'react-redux';
// import 'react-circular-progressbar/dist/styles.css';
import {
    Box,
    Grid,
    IconButton,
    Paper,
    Container,
    Typography,
    Tabs,
    Tab,
} from '@material-ui/core';
import {
    ExpandMore,
    AttachFile
} from '@material-ui/icons';
import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';
import dayjs from 'dayjs';
import SwipeableViews from 'react-swipeable-views';
import clsx from 'clsx';
// import { ListView } from 'antd-mobile';
import { useTheme } from '@material-ui/core/styles';
import { fetchTodoTaskRuns$, getDraftsHz$ } from '@/api';
import AppBar from '@/components/app-bar';
import Divider from '@/components/divider';
import TabPanel from './tab-panel';
import useStyles from './styles';
import ClickLoadMore from '@/components/click-load-more';

const Tasks: React.FC<RouteComponentProps> = props => {
    const classes = useStyles();
    const theme = useTheme();
    const tabList = ['繁殖', '保健', '营养', '任务'];
    const [listMap, setListMap] = useState<IndexMap<any[]>>({
        0: [],
        1: [],
        2: [],
        3: []
    });
    const [pageMap, setPageMap] = useState<IndexMap<number>>({
        0: 1,
        1: 1,
        2: 1,
        3: 1,
    });
    const [nextMap, setNextMap] = useState<IndexMap<string>>({
        0: 'init',
        1: 'init',
        2: 'init',
        3: 'init',
    });
    const [firstFetchData, setFirstFetchData] = useState<IndexMap<boolean>>({
        0: true,
        1: true,
        2: true,
        3: true,
    });
    const [loading, setLoading] = useState(false);
    const [tabValue, setTabValue] = useState(3);
    const dispatch = useDispatch();
    const page_size = 10;

    function handleChange(event: React.ChangeEvent<{}>, newValue: number) {
        setTabValue(newValue);
    }

    const getTask3List = useCallback(() => {
        const ApiInst = fetchTodoTaskRuns$({ page: pageMap[3], page_size }).pipe(
            map(({response}) => response),
            map(response => [response.results, response.next]),
            map(([list, next]) => {
                const tasksList =  list.map((item: any) => {
                    return {
                        ...item,
                        toNowDate: dayjs(item.run_date).diff(dayjs(), 'day')
                    }
                });
                return [tasksList, next];
            })
        ).subscribe(
            (data: any) => {
                console.log('data', data);
                setLoading(false);
                setListMap({ ...listMap, 3: listMap[3].concat(data[0]) });
                setNextMap({ ...nextMap, 3: data[1] });
                firstFetchData[3] && setFirstFetchData({...firstFetchData, 3: false});
            },
            () => setLoading(false)
        );
        return ApiInst;
    }, [pageMap]);
    const getTaskNot3List = useCallback(() => {
        const UsageId: IndexMap<string> = {
            0: '270',
            1: '269',
            2: '268'
        };
        const Params = {
            page: pageMap[tabValue],
            page_size,
            usage_id: UsageId[String(tabValue)]
        };
        const ApiInst = getDraftsHz$(Params).pipe(
            map(({response}) => response),
            map(response => Array.isArray(response) ? [response, null] : [response.results, response.next]),
        ).subscribe(
            (data: any) => {
                console.log('data', data);
                setLoading(false);
                setListMap({ ...listMap, [tabValue]: listMap[tabValue].concat(data[0]) });
                setNextMap({ ...nextMap, [tabValue]: data[1] });
                firstFetchData[tabValue] && setFirstFetchData({...firstFetchData, [tabValue]: false});
            },
            () => setLoading(false)
        );
        return ApiInst;
    }, [tabValue, pageMap]);

    const loadMore = (index: number) => () => {
        if (nextMap[index]) {
            setPageMap({
                ...pageMap,
                [index]: pageMap[index] + 1
            });
        }
    };

    useEffect(() => {
        let ApiInst: Subscription;
        if (pageMap[tabValue]!== 1) {
            setLoading(true);
            ApiInst = tabValue !== 3 ? getTaskNot3List() : getTask3List();
        }
        return () =>  ApiInst && ApiInst.unsubscribe();
    }, [pageMap]);

    useEffect(() => {
        let ApiInst: Subscription;
        if (firstFetchData[tabValue]) {
            setLoading(true);
            ApiInst = tabValue !== 3 ? getTaskNot3List() : getTask3List();
        }
        return () =>  ApiInst && ApiInst.unsubscribe();
    }, [firstFetchData, tabValue, getTaskNot3List, getTask3List]);

    return (
        <Box className={clsx('bg-gray')}>
            <AppBar title="任务清单" />
            <Box className={classes.selectContainer}>
                <Tabs
                    value={tabValue}
                    onChange={handleChange}
                    indicatorColor="secondary"
                    textColor="secondary"
                    centered
                    variant="fullWidth"
                >
                    {
                        tabList.map(item => <Tab key={item} label={item} />)
                    }
                </Tabs>
            </Box>
            <Container>
                <SwipeableViews
                    axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                    index={tabValue}
                    onChangeIndex={(index) => setTabValue(index)}
                >
                    {
                        tabList.map((item, index) => {
                            return (
                                <TabPanel
                                    key={item}
                                    value={tabValue}
                                    index={index}
                                    dir={theme.direction}
                                    // className={classes.pageContainer}
                                >
                                    <Box className={classes.content}>
                                        <Choose>
                                            <When condition={index !== 3}>
                                                {
                                                    listMap[index].map((item: any, i) => (
                                                        <Paper className={classes.paper} key={`tasks-${index}item-${i}`}>
                                                            <Box position="relative">
                                                                <Grid container>
                                                                    <Grid item xs={2} container justify="center" alignItems="center">
                                                                        <Box paddingX="5px">
                                                                            <img
                                                                                className={classes.img}
                                                                                alt="complex"
                                                                                src={item.photo ? item.photo : require('../../assets/images/default.jpg')}
                                                                            />
                                                                        </Box>
                                                                    </Grid>
                                                                    <Grid item xs={10} container>
                                                                        <Grid item xs="auto" container direction="column">
                                                                            <Grid item xs>
                                                                                <Typography gutterBottom variant="subtitle1">
                                                                                    {item.workflow_type_name}
                                                                                </Typography>
                                                                                <Typography variant="subtitle2" gutterBottom>
                                                                                    <Box fontSize="12px">
                                                                                        派工单类型：{item.drafttype}
                                                                                    </Box>
                                                                                    <Box fontSize="12px">
                                                                                        任务日期：{item.event_date}
                                                                                    </Box>
                                                                                    <Box fontSize="12px">
                                                                                        完成期限：{item.activity_done_day}
                                                                                    </Box>
                                                                                    <Box fontSize="12px">
                                                                                        已完成：{item.done_count} / 总数：{item.totalcount}
                                                                                    </Box>
                                                                                </Typography>
                                                                            </Grid>
                                                                            <Grid item>
                                                                                <Divider />
                                                                                <Typography variant="subtitle2" gutterBottom>
                                                                                    <IconButton
                                                                                        onClick={() => props.history.push(`/tasksDetail/${item.id}`)}
                                                                                        className={classes.detailBtn}>
                                                                                        <Box
                                                                                            fontSize="12px"
                                                                                        >
                                                                                            明细
                                                                                        </Box>
                                                                                        <ExpandMore color="primary" />
                                                                                    </IconButton>
                                                                                </Typography>
                                                                            </Grid>
                                                                        </Grid>
                                                                    </Grid>
                                                                </Grid>
                                                            </Box>
                                                        </Paper>
                                                    ))
                                                }
                                            </When>
                                            <Otherwise>
                                                {
                                                    listMap[3].map((item: any, i) => (
                                                        <Paper className={clsx(classes.paper, 'minHeight100')} key={`tasks-3-item-${i}`}>
                                                            <Box position="relative">
                                                                <Grid container>
                                                                    <Grid item xs={2} container justify="center" alignItems="center">
                                                                        <Box paddingX="5px">
                                                                            <img
                                                                                className={classes.img}
                                                                                alt="complex"
                                                                                src={item.photo ? item.photo : require('../../assets/images/default.jpg')}
                                                                            />
                                                                        </Box>
                                                                    </Grid>
                                                                    <Grid item xs={10} container>
                                                                        <Grid item xs="auto" container direction="column">
                                                                            <Grid item xs>
                                                                                <Typography gutterBottom variant="subtitle1">
                                                                                    {item.run_date}
                                                                                </Typography>
                                                                                <Typography variant="subtitle2" gutterBottom>
                                                                                    <Box fontSize="12px">
                                                                                        任务名称：{item.todotask_name}
                                                                                    </Box>
                                                                                    <Box fontSize="12px">
                                                                                        责任人：{item.tasker_name}
                                                                                    </Box>
                                                                                    <Box fontSize="12px">
                                                                                        任务描述：{item.content}
                                                                                    </Box>
                                                                                </Typography>
                                                                            </Grid>
                                                                            <Grid item>
                                                                                <Divider />
                                                                                <Typography variant="subtitle2" gutterBottom>
                                                                                    <IconButton
                                                                                        onClick={() => props.history.push(`/tasksDetail/${item.id}`)}
                                                                                        className={classes.detailBtn}>
                                                                                        <Box
                                                                                            fontSize="12px"
                                                                                        >
                                                                                            明细
                                                                                        </Box>
                                                                                        <ExpandMore color="primary" />
                                                                                    </IconButton>
                                                                                </Typography>
                                                                            </Grid>
                                                                        </Grid>
                                                                    </Grid>
                                                                </Grid>
                                                                <Box
                                                                    position="absolute"
                                                                    right="0%"
                                                                    top="10%"
                                                                >
                                                                    <Choose>
                                                                        <When condition={item.attachment}>
                                                                            <a href={item.attachment} download>
                                                                                <IconButton>
                                                                                    <AttachFile color="primary" />
                                                                                </IconButton>
                                                                            </a>
                                                                        </When>
                                                                        <Otherwise>
                                                                            <IconButton onClick={() => dispatch({
                                                                                type: 'SHOW_MESSAGE',
                                                                                payload: {
                                                                                    open: true,
                                                                                    variant: 'warning',
                                                                                    message: '无可用附件下载'
                                                                                }
                                                                            })}>
                                                                                <AttachFile color="primary" />
                                                                            </IconButton>
                                                                        </Otherwise>
                                                                    </Choose>
                                                                </Box>
                                                            </Box>
                                                        </Paper>
                                                    ))
                                                }
                                            </Otherwise>
                                        </Choose>
                                        <ClickLoadMore
                                            loading={loading}
                                            loadMore={loadMore(index)}
                                            hasMore={Boolean(nextMap[index])}
                                        />
                                    </Box>
                                </TabPanel>
                            )
                        })
                    }
                </SwipeableViews>
            </Container>
        </Box>
    );
}
export default Tasks;